@font-face {
  font-family: metroFont;
  src: url(./assests/Metropolis-Regular.otf);
}

#root {
  background-color: #f0f0f0;
  background-image: url(./assests/Images/Illustration1.png) , url(./assests/Images/Illustration2.png), url(./assests/Images/Illustration3.png), url(./assests/Images/Illustration4.png), url(./assests/Images/Illustration5.png);
  /* background-position: left -15% bottom -15%, right -20% top -20%, left bottom 50%, right -5% bottom -5%, right bottom 50%; */
  background-attachment: fixed;
  background-size: 280px;
  background-position:
    bottom -40px left -60px,
    top -50px right -79px,
    top 30% left 10%,
    bottom -90px right -70px,
    right bottom 50%;
  background-repeat: no-repeat;
  
}

*, body {
  margin: 0;
  
  font-family: metroFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.HomeMainDiv {
  height: 100%;
}


.HomeTitle p, h2 {
  float: right;
}

.HomeTitle p {
  color: #9E9E9E;
}

.HomeMenuDiv {
  margin-top: 100px;
}

.HomeCardHero {
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  margin-bottom: 20px;
  border-radius: 6px;
}
.row> :nth-child(even) .HomeCardHero{
  height: 300px;
}
.row> :nth-child(even) .HomeCardHeroText{
  background-image: linear-gradient(to bottom, rgb(255 255 255 / 0%), rgb(255 255 255) 60%);
  color: black;
}
.row> :nth-child(6) .HomeCardHero,.row> :nth-child(8) .HomeCardHero,.row> :nth-child(10) .HomeCardHero{
  height: 400px;
  margin-top: -100px;
}

.HomeCardHeroText {
  position: absolute;
  width: 100%;
  padding: 10px;
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,1) 60%);
  color: white;
  border-radius: 6px;
}

.HomeCardHeroText p {
  font-size: 10px;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,77,64, 0.7);
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  -webkit-transition: .3s ease;
  transition: .3s ease;
  border-radius: 6px;
}

.overlay h5 {
  text-transform: uppercase;
  text-align: center;
  margin: 10% 10px;
  border: 2px white solid;
  padding: 10px;
  border-radius: 25px;
  color: white;
}
h5:hover {
  text-transform: uppercase;
  text-align: center;
  margin: 10% 10px;
  background-color: white;
  border: 2px white solid;
  padding: 10px;
  border-radius: 25px;
  color: black;
}

.HomeCardHero:hover .overlay {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.DetailMiddleDiv {
  margin-top: 100px;
}



.MiddleVideoHero {
