}

.separator {
  color: #BDBDBD;
  display: flex;
  align-items: center;
  text-align: center;
  margin-top: 200px;
  padding-bottom: 100px;
}
.separator::before, .separator::after {
  content: '';
  flex: 1;
  border-bottom: 1px solid #BDBDBD;
}
.separator::before {
  margin-right: 40px;
}
.separator::after {
  margin-left: 40px;
}

